body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.data-entry-container{padding:20px;background-color:#ff993346}.paper-container{padding:20px}.paper-container .paper-heading{font-size:24px;font-weight:600;text-align:center;border-bottom:1px solid #ff9933;padding-bottom:5px}.paper-container .paper-contents{margin-top:20px;border-bottom:1px solid #ff9933;padding-bottom:20px;margin-bottom:20px;z-index:300 !important}.paper-container .paper-contents .paper-content{margin:10px;display:flex;align-items:center}.paper-container .paper-contents .paper-content .content-title{font-weight:600;width:100px}.paper-container .paper-contents .paper-content .content-select{margin-left:10px;margin-right:10px}.paper-container .paper-contents .paper-content .content-input input{height:30px;border-radius:4px !important;width:100px;outline:none;border-color:#ccc;box-shadow:none;border-style:solid}.paper-container .paper-table{z-index:-1}@media only screen and (max-width: 800px){.paper-content{display:flex;flex-wrap:wrap}.paper-content .content-title{width:300px;margin-top:10px}.paper-content .content-select{width:100%;margin-top:10px}.paper-content .content-input{margin-left:10px;margin-top:10px}.paper-content .content-input input{width:100% !important}}

.app{background-color:#ff993346}

